'<p>В многонациональных городах большинство обитателей вряд ли заинтересуется представителем даже самой экзотической расы, но в маленьких городках и деревнях все по-другому. Обычные люди не привыкли видеть представителей такой расы, и будут вести себя соответственно.' +
'<br /><br />Принято считать, что полуорки агрессивны и легко впадают в ярость, так что люди ведут себя осторожно в присутствии незнакомого полуорка. Торговцы могут тайком прятать наиболее ценные или хрупкие товары при появлении полуорка, и люди потихоньку покидают таверны, опасаясь возможной драки.</p>';

